      <label for="" @click='checkPosition("xianfeng")' id='xianfeng'>
        <input type="checkbox" class="xianfeng">
        <span>先锋</span>
      </label>
      <label for="" @click='checkPosition("jinwei")' id='jinwei'>
        <input type="checkbox" class="jinwei" >
        <span>近卫</span>
