<script>
// @ is an alias to /src
import { DATA } from '@/gameinfo'

export default {
  data () {
    return {
      filter: [],
      type: [],
      data: DATA
    }
  },
  methods: {
    checkStar (id) {
      if (document.documentElement.getElementsByClassName(id)[0].checked) {
        document.getElementById(id).style.backgroundColor = '#fff'
        this.remove(id, this.filter)
      } else {
        document.getElementById(id).style.backgroundColor = 'rgb(58, 155, 231)'
        this.filter.push(id)
      }
      // console.log(document.getElementById(id))
      // document.getElementById(id).style.backgroundColor = 'rgb(58, 155, 231)'
      // console.log(document.documentElement.getElementsByClassName(id)[0].checked)
      document.documentElement.getElementsByClassName(id)[0].checked = !document.documentElement.getElementsByClassName(id)[0].checked
      this.showFilter()
    },
    checkPosition (id) {
      if (document.documentElement.getElementsByClassName(id)[0].checked) {
        document.getElementById(id).style.backgroundColor = '#fff'
        this.remove(id, this.type)
      } else {
        document.getElementById(id).style.backgroundColor = 'rgb(58, 155, 231)'
        this.type.push(id)
      }
      document.documentElement.getElementsByClassName(id)[0].checked = !document.documentElement.getElementsByClassName(id)[0].checked
      this.showFilter()
    },
    remove (val, arr) {
      const index = arr.indexOf(val)
      arr.splice(index, 1)
    },
    showFilter () {
      this.data = []
      for (const index in DATA) {
        if (this.filter.indexOf(DATA[index].star) > -1 || this.filter.length === 0) {
          if (this.type.indexOf(DATA[index].type) > -1 || this.type.length === 0) {
            this.data.push(DATA[index])
          }
        }
      }
    },
    material (index) {
      this.$router.push('/' + index)
    }
  }
}
</script>
