<script>
import { MATERIAL, CAILIAO } from '@/gameinfo'
export default {
  props: ['id'],
  data () {
    return {
      person: MATERIAL,
      cailiao: CAILIAO,
      canUpdate: false,
      drawer: false,
      materialSource: [
        {
          tujing: '',
          gailv: '',
          update: ''
        }
      ],
      forUpdate: []
    }
  },
  methods: {
    getMaterial (id) {
      this.materialSource = []
      const source = this.cailiao[id].source
      // const gailv = this.cailiao[id].gailv
      const update = this.cailiao[id].update
      for (let i = 0; i < source.length; i++) {
        this.materialSource[i] = {
          tujing: source[i]
          // gailv: gailv[i]
        }
      }
      if (update) {
        this.canUpdate = true
      } else {
        this.materialSource[0].update = '无法合成'
        this.canUpdate = false
      }
    },
    showUpdate (id) {
      const update = this.cailiao[id].update
      this.forUpdate = update
      this.drawer = true
    }
    // showme (item) {
    //   const canUpdate = item.update
    //   let resource = item.name + '获取途径：' + item.source
    //   if (canUpdate) {
    //     resource += '。合成方式：'
    //     for (let i = 0; i < canUpdate.length; i++) {
    //       resource += this.cailiao[canUpdate[i][0]].name + '×' + canUpdate[i][1] + '，'
    //     }
    //   }
    //   alert(resource)
    // }
  },
  mounted () {
    this.$message({
      showClose: true,
      message: '数据有误？联系1485500330（QQ）',
      duration: 5000
    })
  }
}
</script>

